.container {
    position: relative;
    width: 90%;
    height: 500px;
    margin: 100px auto 20px auto;
    filter: drop-shadow(0 0 5px black);
}

.background-image {
    width: 100%;
    height: 500px;
    border-radius: 8px;
}

.background-image img {
    height: 100%;
    width: 100%;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

.content {
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    color: #fff;
    padding: 20px;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content .content-text {
    position: relative;
    width: 80%;
    margin: 0 auto;
}

.content h2 {
    color: tomato;
    font-size: 3rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.content h2 span {
    color: blueviolet;
}

.content h3 {
    font-size: 2rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.btn {
    border-radius: 6px;
    font-size: 1.2rem;
    padding: 10px 30px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    bottom: 90px;
    border: 1px solid tomato;
    background: none;
    text-transform: uppercase;
    cursor: pointer;
}
.btn a{
    color: #fff;
}
.btn:hover {
    background-color: orangered;
}

@media screen and (max-width:950px) {
    .btn {
        bottom: 50px;
    }
}

@media screen and (max-width:500px) {
    .content h2 {
        font-size: 2rem;
    }

    .content h3 {
        font-size: 1.8rem;
    }

    .btn {
        font-size: 1rem;
    }
}